 export default{
	 data(){
		return {
			title:'',
			strings:''
		} 
	 },
	 onLoad:function(e){
	 	//接收new页面中传输的id值
		// console.log(e);
		//ure 简写
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/' +e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				//输出新闻的标题
				this.title = res.data.title;
				//输出新闻内容
				this.strings = res.data.content;
			},
			fail: () => {},
			complete: () => {}
		});
	 }
 }
